@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
:root {
  --primary: white;
  --secondary: black;
  --highlight: #0000FF;
  --text-highlight: yellow;
  --card-shadow: #00000044;
  --light-text: #888888;

}

body {
  margin: 0;
  padding: 0;
}
.loader:empty {
  position: absolute;
  top: calc(50% - 4em);
  left: calc(50% - 4em);
  width: 6em;
  height: 6em;
  border: 1.1em solid rgba(0, 0, 0, 0.2);
  border-left: 1.1em solid #3a3a3a;
  border-radius: 50%;
  -webkit-animation: load8 1.1s infinite linear;
          animation: load8 1.1s infinite linear;
}

@-webkit-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

html {
  font-family: 'Raleway', sans-serif;
}
.homeContainer {
  height: calc(100vh - 75px);
  width: 100vw;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  overflow: hidden;
}

.homeContainer>h1 {
  font-size: 7em;
  padding-top: 16%;
  margin: 0;
  text-align: center;
}

.homeContainer>h2 {
  text-align: center;
}

.homeContainer>.buttonContainer {
  left: calc(50% - 25vw);
  width: 50vw;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.homeContainer>.buttonContainer>form {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.homeContainer>.buttonContainer>form>input {
  padding: 10px;
  margin: 10px;
  min-width: 180px;
  max-width: calc(15vw - 20px);
  width: calc(40% - 20px);

}

.homeContainer .Link {
  padding: 10px 15px 10px 15px;
  text-decoration: none;
  border-radius: 10px;
  font-size: 24px;
  color: var(--primary);
  background-color: var(--secondary);
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
}

@media screen and (max-width: 768px) {}
/* grid container for newgame component */
.newGameContainer {
  height: calc(100vh - 75px);
  width: 100vw;
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
  grid-template-rows: 1fr 80px;
  grid-template-areas: 'p s s'
                       'p b .';
}

/* cardset select styling */
.highlighted {
  background-color: var(--highlight);
  color: white;
  font-size: 36px;
  border-radius: 5px;
  padding: 8px;
  margin: 15px;
  height: 45px;
  cursor: pointer;
}

.unhighlighted {
  background-color: var(--secondary);
  color: white;
  font-size: 36px;
  border-radius: 5px;
  padding: 8px;
  margin: 15px;
  height: 45px;
  cursor: pointer;
}

.highlighted:hover, .unhighlighted:hover {
  color: var(--text-highlight);
}

.setContainer {
  grid-area: s;
  display: flex;
  flex-wrap: wrap;
  padding: 15px;
}

.setContainer > h1 {
  font-size: 48px;
  width: 100%;
  padding: 0 15px 0 15px;
}

/* current players styling */
.playersContainer {
  text-align: center;
  grid-area: p;
  border: .5px solid black;
  margin: 25px 25px 0 25px;
}

.playersContainer > h1 {
  border-bottom: .5px solid black;
  padding: 5px;
  font-size: 40px;
}

.players {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  grid-area: p;
  border-right: black solid 1px;
  overflow: auto;
  font-size: 1.2em;
}

.players > ul {
  padding: 0;
}

.player > img {
  width: 100px;
  height: 100px;
  border-radius: 100%;
  align-self: flex-start;
  margin: 0 0 0 15px;
}

.gameName {
  grid-area: n;
}

.startForm {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-content: center;
}

.startForm input {
  width: 60%;
  height: 40%;
  margin: 1%;
  padding: 5px;
  font-size: 1.2em;
}

.startButton {
  grid-area: b;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
}

.startButton button {
  text-align: center;
  text-decoration: none;
  padding: 10px;
  border-radius: 10px;
  font-size: 24px;
  color: var(--primary);
  background-color: var(--secondary);
  width: 30%;
  height: 70%;
  border: .5px solid var(--secondary);
}

.lobbyFail {
  width: 50%;
  height: 70%;
  background-color: rgba(255, 0, 0, 0.911);
  border-radius: 10px;
  margin: 0 10px 0 10px;
  color: var(--primary);
  display: flex;
  justify-content: center;
}

.lobbyFail > span {
  height: 1em;
  align-self: center;
}
.player {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-transform: capitalize;
}

.player > p {
  color: var(--light-text);
  margin-top: 0px;
}

.player > h3 {
  margin-bottom: 0px;
}
.game-screen {
  height: calc(100vh - 75px);
  width: 100vw;
  display: grid;
  grid-template-columns: 1fr 5fr 1fr;
  grid-template-rows: 4fr 1fr;
  grid-template-areas: "p a c"
    "w w w";
  overflow: hidden;

}

.game-screen .players {
  text-align: center;
  grid-area: p;
  /* background: blue; */
  border-right: black solid 1px;
  overflow: auto;
}

.game-screen .players ul {
  padding: 0px;
}


.game-screen .play-area {
  width: 5fr;
  grid-area: a;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow-x: auto;
  overflow-y: hidden;
  white-space: nowrap;
  /* background: green; */
}

.game-screen .start-area {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.game-screen .play-area h1 {
  font-size: 3.5em;
}

.game-screen .play-area button {
  color: var(--primary);
  background-color: var(--secondary);
  border: .5px solid var(--secondary);
  text-align: center;
  text-decoration: none;
  width: 10vw;
  height: 6vh;
  border-radius: 10px;
  font-size: 1.2em;
}

.game-screen .play-area>* {
  white-space: pre-wrap;
}

.game-screen .chat-area {
  grid-area: c;
  border-left: 1px black solid;
  /* background: red; */
}

.game-screen .white-cards {
  border-top: black 1px solid;
  grid-area: w;
  background: var(--card-shadow);
  /* transform: translateY(50%); */
  -webkit-transition: -webkit-transform .3s;
  transition: -webkit-transform .3s;
  transition: transform .3s;
  transition: transform .3s, -webkit-transform .3s;
  height: 185%;
}

.game-screen .white-cards:hover {
  -webkit-transform: translateY(-46%);
          transform: translateY(-46%);

}

.game-screen .white-cards button {
  padding: 20px;
  border: none;
  border-radius: 10px;
  background: var(--primary);
}

.game-screen .white-cards>.inactive {
  text-align: center;
}
.game-screen .white-cards ul {
  display: flex;
  list-style: none;
  justify-content: space-around;
  align-items: center;
  padding: 0px;
  margin: 0px;
  height: 100%;
}

.game-screen .white-cards li {
  display: flex;
  width: 9.5%;
  height: 80%;
  font-size: 1em;
  align-items: center;
  justify-content: center;
  /* transition: width .3s, height .3s, font-size .3s, font-weight .3s;
  -webkit-transition: width .3s, height .3s, font-size .3s, font-weight .3s; */
  -webkit-transition: -webkit-transform .3s;
  transition: -webkit-transform .3s;
  transition: transform .3s;
  transition: transform .3s, -webkit-transform .3s;


}

.game-screen .white-cards li:hover {
  /* width: 12%;
  height: 100%;
  font-size: 1.12em;
  font-weight: bold; */
}

.game-screen .white-cards .white-card {
  z-index: 1;
  position: relative;
  display: flex;
  cursor: pointer;
  /* border: 1px black solid; */
  height: 80%;
  width: 100%;
  border-radius: 10px;
  text-align: left;
  justify-content: flex-start;
  align-items: flex-start;
  background: var(--primary);
  padding-left: 5px;
  padding-right: 5px;
  /* border-left: solid black 1px; */
  -webkit-transition: background .3s, box-shadow .3s, -webkit-transform .3s;
  transition: background .3s, box-shadow .3s, -webkit-transform .3s;
  transition: background .3s, box-shadow .3s, transform .3s;
  transition: background .3s, box-shadow .3s, transform .3s, -webkit-transform .3s;
  -webkit-transition: background .3s, box-shadow .3s, transform .3s;
  /* border: 1px black solid; */

}
.game-screen .white-cards .white-card:hover {
  /* box-shadow: 10px 10px 5px var(--card-shadow); */
  /* transform: translateX(-5px) translateY(-15px) scale(1.2); */
  -webkit-transform: rotate(8deg) scale(1.1);
          transform: rotate(8deg) scale(1.1);
  z-index: 0;
}


.white-card p {
  width: 100%;
  /* word-wrap: break-word; */
}



.game-screen .white-cards .white-card[class$="selected"] {
  /* background: var(--text-highlight) */
  -webkit-transform: translateY(-20px);
          transform: translateY(-20px);
}

.game-screen .white-cards .white-card[class$="selected"]:hover {
  -webkit-transform: translateY(-20px) rotate(8deg) scale(1.1) ;
          transform: translateY(-20px) rotate(8deg) scale(1.1) ; 

}

.game-screen .white-cards .white-card .overlay {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background: rgba(50, 50, 50, 0.6);
  border-radius: 10px;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0;
  -webkit-transition: opacity .3s;
  transition: opacity .3s;
  color: var(--primary);
  
}
.game-screen .white-cards .white-card .overlay:hover {
  opacity: 1;
}
.lwc {
  /* background: var(--secondary);
  color: var(--primary); */
  width: calc(25% - 20px);
  height: 65%;
  margin: 20px;
  font-size: 2em;
  font-weight: bold;
  padding: 10px;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border: solid black 1px;
  min-width: 25%;
}

.lwc p {
  margin: 5px 0px;
}



.lwc>.hidden {
  margin: auto;
  text-align: center;
}

.winning-card {
  box-shadow: 5px 5px 5px
}

.lwc div {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

}

.lwc div p:last-child {
  text-align: right;
}
.black-card {
  background: var(--secondary);
  color: var(--primary);
  width: calc(25% - 20px);
  height: 65%;
  margin: 20px;
  min-width: 25%;
  font-size: 2em;
  font-weight: bold;
  padding: 10px;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: -webkit-sticky;
  position: sticky;
  left: 20px;
}

.black-card p {
  margin: 5px 0px;
}

.black-card div:last-child {
  text-align: right;
  font-size: .8em;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.black-card>div:last-child>div {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background: var(--primary);
  color: var(--secondary);
  border-radius: 100%;
  width: 30px;
  height: 30px;
  font-size: 1.1em;
}
.chat-area .username {
  font-weight: bold;
}

.chat-area ul {
  list-style: none;
  height: calc(60vh - 75px);
  overflow-y: auto;
}

.chat-area h1 {
  text-align: center;
}

.chat-area {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.chat-area > form > button {
  display: none
}

.chat-area > form > input {
  width: calc(100% - 22px);
  height: 30px;
  padding: 5px;
  margin: 5px;

}
.navbar {
  width: calc(100% - 60px);
  height: 65px;
  padding: 0px 30px 0px 30px;
  background: var(--secondary);
  font-size: 1.05em;
  display: flex;
  justify-content: space-between;
  align-content: center;
}

.navbar>h2 {
  color: var(--primary);
  margin: 0;
  align-self: center;
}

.navbar>ul {
  width: 50%;
  height: 100%;
  padding: 0px;
  margin: 0px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.navbar>ul>li {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 100%;
}

.navbar>ul a {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  height: 100%;
}

.navbar>ul li div {
  width: 0;
  height: 0;
  background-color: rgb(53, 53, 240);
  border-radius: 10px;
}

.navbar a {
  height: 100%;
  width: 100%;
  color: var(--primary);
  text-decoration: none;
  text-align: center;
  font-weight: bold;
  border-radius: 5px;
}

.navbar li:hover div {
  -webkit-transition: 400ms;
  transition: 400ms;
  width: 100%;
  height: 10%;
  align-self: flex-end;
}
.authContainer {
  width: 100vw;
  height: 100vh;
  padding: 1% 0 0 0;
  text-align: center;
}

.authContainer > h1 {
  font-size: 4em;
  letter-spacing: 2px;
  margin: 5%;
}

.signup {
  align-self: center;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 30%;
  height: 50%;
  border: 1px solid #7a7a7a;
  background-color: #ececec;
  border-radius: 10px;
  margin: auto;
  padding: 10px;
}

.signup span {
  font-size: .8em;
}

.signup a {
  text-decoration: none;
}

.signup > form {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 5px;
}

.signup button{
  width: 100px;
  height: 40px;
  border-radius: 10px;
}

.signup input {
  display: block;
  padding: 2px 0 2px 15px;
  font-size: .9em;
  width: 75%;
  height: 10%;
  border-radius: 5px;
  border: .5px solid #cccccc;
}

.loginFail {
  width: 70%;
  height: 8%;
  border-radius: 10px;
  background-color: rgba(255, 0, 0, 0.836);
  text-align: center;
  padding: 5px;
  font-size: 18px;
  color: white;
}
