.authContainer {
  width: 100vw;
  height: 100vh;
  padding: 1% 0 0 0;
  text-align: center;
}

.authContainer > h1 {
  font-size: 4em;
  letter-spacing: 2px;
  margin: 5%;
}

.signup {
  align-self: center;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 30%;
  height: 50%;
  border: 1px solid #7a7a7a;
  background-color: #ececec;
  border-radius: 10px;
  margin: auto;
  padding: 10px;
}

.signup span {
  font-size: .8em;
}

.signup a {
  text-decoration: none;
}

.signup > form {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 5px;
}

.signup button{
  width: 100px;
  height: 40px;
  border-radius: 10px;
}

.signup input {
  display: block;
  padding: 2px 0 2px 15px;
  font-size: .9em;
  width: 75%;
  height: 10%;
  border-radius: 5px;
  border: .5px solid #cccccc;
}

.loginFail {
  width: 70%;
  height: 8%;
  border-radius: 10px;
  background-color: rgba(255, 0, 0, 0.836);
  text-align: center;
  padding: 5px;
  font-size: 18px;
  color: white;
}