/* grid container for newgame component */
.newGameContainer {
  height: calc(100vh - 75px);
  width: 100vw;
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
  grid-template-rows: 1fr 80px;
  grid-template-areas: 'p s s'
                       'p b .';
}

/* cardset select styling */
.highlighted {
  background-color: var(--highlight);
  color: white;
  font-size: 36px;
  border-radius: 5px;
  padding: 8px;
  margin: 15px;
  height: 45px;
  cursor: pointer;
}

.unhighlighted {
  background-color: var(--secondary);
  color: white;
  font-size: 36px;
  border-radius: 5px;
  padding: 8px;
  margin: 15px;
  height: 45px;
  cursor: pointer;
}

.highlighted:hover, .unhighlighted:hover {
  color: var(--text-highlight);
}

.setContainer {
  grid-area: s;
  display: flex;
  flex-wrap: wrap;
  padding: 15px;
}

.setContainer > h1 {
  font-size: 48px;
  width: 100%;
  padding: 0 15px 0 15px;
}

/* current players styling */
.playersContainer {
  text-align: center;
  grid-area: p;
  border: .5px solid black;
  margin: 25px 25px 0 25px;
}

.playersContainer > h1 {
  border-bottom: .5px solid black;
  padding: 5px;
  font-size: 40px;
}

.players {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  grid-area: p;
  border-right: black solid 1px;
  overflow: auto;
  font-size: 1.2em;
}

.players > ul {
  padding: 0;
}

.player > img {
  width: 100px;
  height: 100px;
  border-radius: 100%;
  align-self: flex-start;
  margin: 0 0 0 15px;
}

.gameName {
  grid-area: n;
}

.startForm {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-content: center;
}

.startForm input {
  width: 60%;
  height: 40%;
  margin: 1%;
  padding: 5px;
  font-size: 1.2em;
}

.startButton {
  grid-area: b;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
}

.startButton button {
  text-align: center;
  text-decoration: none;
  padding: 10px;
  border-radius: 10px;
  font-size: 24px;
  color: var(--primary);
  background-color: var(--secondary);
  width: 30%;
  height: 70%;
  border: .5px solid var(--secondary);
}

.lobbyFail {
  width: 50%;
  height: 70%;
  background-color: rgba(255, 0, 0, 0.911);
  border-radius: 10px;
  margin: 0 10px 0 10px;
  color: var(--primary);
  display: flex;
  justify-content: center;
}

.lobbyFail > span {
  height: 1em;
  align-self: center;
}